import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { TransferModalSheetPage } from '../transfer-modal-sheet/transfer-modal-sheet.page';

import { TransferPage } from './transfer.page';
import { ManageAccountsPage } from '../manage-accounts/manage-accounts.page';

const routes: Routes = [
  {
    path: '',
    component: TransferPage
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes)
  ],
  declarations: [TransferPage,TransferModalSheetPage,ManageAccountsPage],
  entryComponents:[TransferModalSheetPage,ManageAccountsPage]
})
export class TransferPageModule {}
