import { Component, OnInit } from '@angular/core';
import { ModalController, NavController,MenuController } from '@ionic/angular';
import { ManageAccountsPage } from '../manage-accounts/manage-accounts.page';
import { TransferModalSheetPage } from '../transfer-modal-sheet/transfer-modal-sheet.page';

@Component({
  selector: 'app-transfer',
  templateUrl: './transfer.page.html',
  styleUrls: ['./transfer.page.scss'],
})
export class TransferPage implements OnInit {

  constructor(public modalController: ModalController,
     public navCtrl: NavController,
     public menuCtrl:  MenuController) { }

  ngOnInit() {
  
  }

  openActionmodalsheet(){
    this.openModel();
  }
  async openModel(){
    const myModel =await this.modalController.create({
      component: TransferModalSheetPage,
      cssClass: 'transfer-modal-css' 
    });
    await myModel.present();
   }


   async openManageAccounts(){
    const myModel =await this.modalController.create({
      component: ManageAccountsPage,
      cssClass: 'manage-account-modal-css' 

    });
    await myModel.present();
   }

   goToDashboard(){
    this.navCtrl.navigateBack("dashboard");
  }

  ionViewWillEnter() {
    this.menuCtrl.enable(false);
  }
  goBack(){
    this.navCtrl.navigateBack('dashboard')
  }
}
